import React,{Fragment,useState} from "react"
import StatCard from "../Stat/Card/StatCard";

const StatGrid = ({trends,setModal,showHandler,setModalData})=>{
    
	 	return(
      <Fragment>

        <h2 className="text--white mx-2">Stats</h2>
        <div className="cards">
           {trends && trends.map(trend => (
              <StatCard key={trend} t={trend} setModal={setModal} setModalData={setModalData} showHandler={showHandler}/>
           ))}
        </div>
      </Fragment>
	)
}

export default StatGrid